  --tequilasSliderPos: 0;
  --color-header-light: #fff;
  --color-header-dark: $color-menu;
  --color-header: var(--color-header-light);
  --gutter-space: 100px;
  --header-height: calc(var(--gutter-space) * 1);
  --footer-height: calc(var(--gutter-space) * 1);
  --margin:  1em;
}

@media screen and (min-width: 1024px) {
  :root {
    --header-height: calc(var(--gutter-space) * 1.1);
