.tequila-modal__data span {
  font-weight: 700;
}

.mobile .tequila-modal__data {
  height: 60%;
  position: static;
  padding: 0 2em;
}

.mobile .tequila-modal__data h2 {
  font-size: 1em;
  text-align: center;
}

.mobile .tequila-modal__data p {
  font-size: .8em;
}

.tequila-modal__social-network {
  margin: 1em 0;
  display: flex;
}

.tequila-modal__social-network li {
  height: 1.3em;
  width: 1.3em;
  margin-right: 0.65em;
}

.tequila-modal__social-network li:last-child {
  margin-right: 0;
}

.tequila-modal__social-network a {
  height: 1.3em;
  width: 1.3em;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.tequila-modal__social-network img {
  height: 1.3em;
  width: auto;
}

.close-modal {
  position: absolute;
  top: 0;
  right: calc(var(--margin) * 2);
  background: transparent;
  border: none;
  font-size: 1.3em;
  cursor: pointer;
}

@media screen and (min-width: 1500px) {
  .close-modal {
    top: calc(var(--margin) * -1);
  }
}

.mobile .close-modal {
  position: fixed;
  top: 0;
  right: 1em;
}

.arrowDownModal {
  display: none;
}

.mobile .arrowDownModal {
  opacity: .75;
  display: block;
  position: absolute;
  width: 2em;
  height: auto;
  left: 50%;
  bottom: var(--footer-height);
  transform: translateY(0.5em) translateX(-50%);
  z-index: calc(var(--z-modal) + 1000);
}

.mobile .brand {
  display: none;
}

.fichaTecnica a {
  font-weight: 700;
}

.fichaTecnica a:hover {
  text-decoration: underline;
}

#layer-back {
  position: absolute;
  display: block;
  width: 100%;
  height: 100vh;
  max-width: 0;
  top: 0;
  right: 0;
  background-color: #F1F2F2;
  z-index: var(--z-layer);
  transition: max-width 0.5s;
}

#layer-back.inicio {
  max-width: 0;
}

#layer-back.nosotros, #layer-back.destileria, #layer-back.contacto {
  max-width: 50%;
}

#layer-back.tequilas {
  max-width: 100%;
}

#layer-back.tequilas::before {
  content: "";
  display: block;
  position: absolute;
  width: 10px;
  height: 100vh;
  left: 0;
  top: 0;
  background-color: #007bdf;
}

.mobile #layer-back.tequilas::before {
  background: transparent;
}

.mobile #layer-back:not(.inicio) {
  max-width: 100% !important;
}

.main {
  position: absolute;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  top: 0;
  left: 0;
  background: url(../img/Landscape.jpg) center/cover no-repeat;
}

.section {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: var(--z-normal);
}

.section .section__content,
.section .tequilas__content {
  opacity: 0;
}

.section.active {
  z-index: var(--section-active);
}

.section.active .section__content,
.section.active .tequilas__content {
  animation: active 0.3s 0.5s forwards;
}

@keyframes active {
  to {
    opacity: 1;
  }
}

.section.nosotros, .section.destileria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.mobile .section.nosotros, .mobile .section.destileria {
  display: block;
}

.section__banner {
  width: 100%;
  height: 100%;
  background: #000;
  overflow: hidden;
}

.section__banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  opacity: 0.8;
}

.section__content {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: calc(var(--header-height) / 2);
}

.mobile .section__content {
  padding-left: 1em;
  padding-right: 1em;
}

.section__text {
  width: calc(100% - calc(var(--margin) * 2));
  height: auto;
  max-height: calc( 100vh - (calc(var(--header-height) * 0.5) - var(--footer-height)));
  font-size: 0.925vw;
  overflow: auto;
  display: block !important;
}

@media screen and (min-width: 1500px) {
  .section__text {
    max-height: calc( 100vh - (calc(var(--header-height) * 2) - var(--footer-height)));
    font-size: 1em;
  }
}

.mobile .section__text {
  font-size: 0.9em;
  max-height: calc(100vh - 250px) !important;
}

.section__text p {
  text-align: justify;
}

.mobile .section__banner {
  display: none;
}

.bannerSlider {
  position: relative;
  width: 100%;
  height: 100%;
  background: #000;
}

.bannerSlider img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-position: center;
  object-fit: cover;
  opacity: 0;
  transition: opacity 0.5s;
}

.bannerSlider img.active {
  opacity: 0.7;
}

.arrowdown {
  position: absolute;
  height: 1.5em;
  width: 3em;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%) translateY(-3em);
}

.mobile .arrowdown {
  transform: translateX(-50%) translateY(-70px);
}

.destileria .section__text {
  max-height: calc( 100vh - (calc(var(--header-height) * 1.2) - var(--footer-height))) !important;
}

.mobile .destileria .section__text,
.mobile .nosotros .nosotros__text {
  max-height: calc(100vh - 230px) !important;
  transform: translateY(-2em);
  padding-bottom: 3em;
}

.active .section__banner img {
  animation: scale 0.5s forwards;
}

@keyframes scale {
  to {
    transform: scale(1.03);
  }
}

.inicio {
  display: block;
  position: relative;
  width: 100%;
  height: 100vh;
}

.inicio::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #000;
  opacity: .3;
  z-index: 20;
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
  opacity: 0;
  transition: opacity .5s;
}

.slide.active {
  opacity: 1;
  z-index: 10;
}

.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

body:not(.mobile) .nosotros .arrowdown {
  display: none;
}

.section__title, .section__subtitle {
  display: table;
  margin: 0 auto;
  font-weight: normal;
  text-transform: uppercase;
}

.section__subtitle {
  margin-bottom: .5em !important;
}

.section__group, .section__title {
  position: relative;
  margin-bottom: 4em !important;
}

.section__group::after, .section__title::after {
  content: '';
  position: absolute;
  display: block;
  width: 1px;
  height: 3em;
  left: 50%;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  transform: translateY(calc(100% + .5em)) translateX(-50%);
}

.section__title {
  line-height: 2em;
  padding: 0 1em;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.5);
}

.section__group:last-child {
  margin-bottom: 0 !important;
}

.section__group:last-child::after {
  display: none;
}

.tequilas__content {
  width: 100%;
  height: 100%;
  position: relative;
}

.tequilas__container {
  width: 100%;
  height: calc( 100vh - var(--header-height) - calc(var(--footer-height) * 1.6));
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(calc(-50% + 1em));
  padding: 0 var(--margin);
}

.mobile .tequilas__container {
  padding: 1em;
}

.tequilas__slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: calc( 100vh - var(--header-height) - calc(var(--footer-height) * 1.6));
  display: flex;
  justify-content: center;
  align-items: center;
}

.overflowHiden {
  width: calc(100% - 6em);
  overflow: hidden;
  height: calc(100vh - var(--header-height) - calc(var(--footer-height) * 1.6));
}

.gallery {
  width: 200%;
  height: calc(100vh - var(--header-height) - calc(var(--footer-height) * 1.6));
  display: flex;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 1em;
  transform: translateX(var(--tequilasSliderPos));
  transition: transform 0.3s;
}

.mobile .gallery {
  width: 1000%;
}

.gallery__item {
  flex: auto;
  font-family: "Lucida Grande", "sans-serif";
  width: 100%;
  height: calc( 100vh - var(--header-height) - calc(var(--footer-height) * 1.6));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.2em;
}

.gallery__item p {
  margin: 0;
  text-align: center;
}

.gallery__item p:last-of-type, .gallery__item p:last-child {
  margin-bottom: 1em;
}

.gallery__item img {
  display: block;
  width: auto;
  height: 50%;
  margin-bottom: 1em;
  transform-origin: bottom;
  transition: transform 0.3s;
  transform: scale(1);
}

.gallery__item:hover img {
  transform: scale(1.1);
}

.gallery__item button {
  background: transparent;
  border: none;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.5);
  line-height: 2.5;
  padding: 0 1em;
  cursor: pointer;
}

.btn-nav {
  position: absolute;
  width: 4em;
  height: 4em;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  background: transparent;
  cursor: pointer;
  transition: opacity 0.3s;
}

.btn-nav:focus, .btn-nav:active {
  outline: none;
  box-shadow: none;
}

.btn-nav img {
  display: block;
  width: 100%;
  height: 100%;
}

.btn-nav.previus {
  left: 0;
  opacity: 0;
}

.btn-nav.next {
  right: 0;
  opacity: 0;
}

.btn-nav.show {
  opacity: 1;
}

.mobile .gallery__item:hover img {
  transform: none;
}

.contact {
  --line-height: 1.5em;
  width: 60%;
  height: 60vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.mobile .contact {
  width: 90%;
}

@media screen and (min-width: 1024px) and (max-width: 1499px) {
  .contact {
    width: 80%;
  }
}

.contact-form {
  font-family: "Myriad Pro", " sans-serif";
}

.form-group {
  position: relative;
  margin-bottom: 1em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
  display: grid;
  grid-template-columns: 70px 1fr;
  align-items: center;
}

.form-group:nth-child(2) {
  margin-bottom: 0.2em !important;
}

.form-group label,
.form-group input,
.form-group textarea {
  line-height: var(--line-height);
  font-family: inherit;
  font-size: 0.9vw;
  border: none;
}

.form-group input,
.form-group textarea {
  background: transparent;
}

.form-group input:-webkit-autofill,
.form-group textarea:-webkit-autofill {
  background: transparent;
}

.form-group input:focus, .form-group input:valid, .form-group input:invalid,
.form-group textarea:focus,
.form-group textarea:valid,
.form-group textarea:invalid {
  outline: none;
  box-shadow: none;
}

.form-group label {
  line-height: 1;
  font-weight: bold;
  text-transform: uppercase;
}

.form-group input {
  padding: 0 0.5em 0 1em;
}

.form-group textarea {
  resize: none;
}

.message {
  border: none !important;
  position: relative;
}

.message label {
  padding-top: 0.7em !important;
  align-self: normal;
}

.form_footer {
  transform: translateY(2.5em);
  display: grid;
  grid-template-columns: calc(var(--margin) * 2) 1fr;
  grid-column-gap: 1em;
}

@media screen and (min-width: 1500px) {
  .form_footer {
    transform: translateY(1em);
    grid-template-columns: calc(var(--margin) * 1.5) 1fr;
  }
}

.form_footer button.button.send {
  font-family: inherit;
  text-transform: uppercase;
  background: #000;
  border-radius: 0;
  line-height: 2.5em;
}

.form_footer .messages {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form_footer .messages img {
  width: 2em;
  height: 2em;
}

.form_footer .messages p {
  text-align: center;
}

.form_footer .messages.success,
.form_footer .messages p.success,
.form_footer .messages.success p {
  color: darkgreen;
}

.form_footer .messages.error,
.form_footer .messages p.error,
.form_footer .messages.error p {
  color: darkred;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px transparent inset;
  transition: background-color 5000s ease-in-out 0s;
}

.contact__data {
  font-family: "Helvetica", "sans-serif";
  font-size: 0.9vw;
}

.contact__data h2 {
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 0.5rem;
  font-size: 1em;
  padding-top: 0.5em !important;
}

.contact__data p {
  margin: 0 !important;
}

.social-network-contact {
  margin-top: 1em;
  display: flex;
}

.social-network-contact li {
  margin-left: 0.5em;
}

.social-network-contact a {
  display: flex;
  width: 1.5em;
  height: 1.5em;
}

.social-network-contact a img {
  height: 1.5em;
  width: auto;
}

.mobile .form-group label,
.mobile .form-group input,
.mobile .form-group textarea {
  font-size: 0.8em;
}

.mobile .form_footer {
  grid-template-columns: 30% 1fr;
}

.mobile .form_footer button.button.send {
  font-size: 0.8em !important;
  line-height: 1.5em !important;
}

.mobile .contact__data {
  font-size: 0.8em;
}

.message {
  --line: 2.5em;
  height: auto !important;
  margin-bottom: 0 !important;
}

.message::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 1px;
  background: #000;
  top: 2.5em;
  left: 0;
  box-shadow: 0 2.5em #000, 0 5em #000, 0 7.5em #000;
  opacity: 0.5;
}

.message label {
  padding-top: 1em !important;
}

.message .textarea {
  padding: 0 1em;
  height: 10em;
  line-height: calc(2.5em - 0.2em);
}

.mobile .message {
  --line: 1.9em !important;
  transform: translateY(-0.8em);
  z-index: 110;
}

.mobile .message:before {
  box-shadow: 0 1.9em #000, 0 3.8em #000, 0 5.7em #000;
}

.mobile .message label {
  padding-top: 1.9em !important;
}

.mobile .message textarea {
  transform: translateY(1em);
  height: calc(var(--line) * 4.8);
  line-height: calc(var(--line) * 1.2) !important;
  font-size: 0.8em !important;
  padding: 0 0.5em !important;
}

.mobile .form-group {
  position: relative;
  z-index: 120;
  margin-bottom: 0.6em;
}

.mobile .form_footer {
  margin-top: -1.2em;
}

.mobile .inicio .slide:nth-child(4) img {
  object-position: 60% center;
}

.mobile .slogan-mobile {
  display: none;
}

.mobile .main-header {
  transform-origin: center;
  transform: translateY(-1em);
}
